(function($) {
	function removeUrlParameter(paramKey) {
		const url = window.location.href;
		var r = new URL(url);
		r.searchParams.delete(paramKey);
		const newUrl = r.href;
		window.history.pushState({ path: newUrl }, '', newUrl);
	}

	$(document).ready(function(){
		removeUrlParameter("mode");
	});
})(jQuery);